// export default Header;

"use client"
// Dependencies
import React, { Component } from "react";
import { Link } from "react-scroll";

// Assets
import "./Header.module.css";

// Data
import dataNavigation from "../../data/navigation.json";

class Header extends Component {
  state = {
    burgerBtnClass: "icon-menu",
    menuActive: false
  };

  handleBurgerMenu = ev => {
    this.setState(prevState => {
      const burgerBtnClass =
        prevState.burgerBtnClass === "icon-menu" ? "icon-cross" : "icon-menu";
      const menuActive = !prevState.menuActive;
      return { burgerBtnClass, menuActive };
    });
  };

  handleLink = ev => {
    if (this.state.menuActive) {
      this.handleBurgerMenu();
    }
  };

  render = () => {
    const { menuActive, burgerBtnClass } = this.state;
    return (
      <header className="header">
        <i
          onClick={this.handleBurgerMenu}
          className={`${burgerBtnClass} burger-button`}
          id="burger-button"
        />
        <div className="container">
          <Link
            activeClass="active"
            to={"app"}
            spy={true}
            smooth={true}
            duration={500}
            onClick={this.handleLink}
          >
            <figure>
              <img alt="logo de frudev" src="images/logo.svg" />
            </figure>
          </Link>
          <nav className={`menu ${menuActive ? "is-active" : ""}`}>
            <ul>
              {dataNavigation.map((item, idx) => (
                <li key={idx}>
                  <Link
                    activeClass="active"
                    to={item.path}
                    spy={true}
                    smooth={true}
                    duration={500}
                    onClick={this.handleLink}
                  >
                    {item.label}
                  </Link>
                </li>
              ))}
            </ul>
          </nav>
        </div>
      </header>
    );
  };
}

export default Header;
